* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.container {
  margin: 30px;
}

.container h1 {
  font-weight: 600;
  font-size: 40px;
  line-height: 160%;
  color: #011B33;
}

.graficos {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pos-center {
  /* position: fixed; */
  position: absolute;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
}